.main {
  width: auto;
  max-width: 1024px;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 0;
  padding-right: 0;
}
.main::after {
  content: "";
  display: table;
  clear: both;
}

.items {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  float: left;
  clear: none;
  text-align: inherit;
  width: 20%;
  margin-left: 0%;
  margin-right: 0%;
  -webkit-transition: width 200ms ease;
  -o-transition: width 200ms ease;
  transition: width 200ms ease;
}
.items::after {
  content: "";
  display: table;
  clear: both;
}
.items:nth-child(n) {
  margin-right: 0%;
  float: left;
  clear: none;
}
.items:nth-child(5n) {
  margin-right: 0%;
  float: right;
}
.items:nth-child(5n + 1) {
  clear: both;
}
@media only screen and (max-width: 700px) {
  .items {
    float: left;
    clear: none;
    text-align: inherit;
    width: 33.3333333333%;
    margin-left: 0%;
    margin-right: 0%;
  }
  .items::after {
    content: "";
    display: table;
    clear: both;
  }
  .items:nth-child(n) {
    margin-right: 0%;
    float: left;
    clear: none;
  }
  .items:nth-child(3n) {
    margin-right: 0%;
    float: right;
  }
  .items:nth-child(3n + 1) {
    clear: both;
  }
}
@media only screen and (max-width: 450px) {
  .items {
    float: left;
    clear: none;
    text-align: inherit;
    width: 50%;
    margin-left: 0%;
    margin-right: 0%;
  }
  .items::after {
    content: "";
    display: table;
    clear: both;
  }
  .items:nth-child(n) {
    margin-right: 0%;
    float: left;
    clear: none;
  }
  .items:nth-child(2n) {
    margin-right: 0%;
    float: right;
  }
  .items:nth-child(2n + 1) {
    clear: both;
  }
}
.items img {
  width: 100%;
}



